
@import '~common/fonts/iconfont.css';
#user .user-info {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
  align-items: center;
  margin-top: 0.4rem;
  height: 2rem;
  width: 100%;
  background-color: #fff;
}
#user .user-info .avatar {
  margin-left: 0.266666666666667rem;
}
#user .user-info .avatar > img {
  height: 1.546666666666667rem;
  width: 1.546666666666667rem;
  border-radius: 50%;
}
#user .user-info .name {
  margin-left: 0.533333333333333rem;
}
#user .features-warpper .cell {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
  align-items: center;
  padding: 0.4rem 0;
  background-color: #fff;
  margin-top: 0.4rem;
}
#user .features-warpper .cell > i {
  margin-left: 0.266666666666667rem;
  display: block;
  height: 0.533333333333333rem;
  width: 0.533333333333333rem;
  background-image: url("./image/u@2x.png");
  background-size: 100%;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
#user .features-warpper .cell > i {
    background-image: url("./image/u@3x.png");
}
}
#user .features-warpper .cell > span {
  margin-left: 0.533333333333333rem;
  font-size: 0.32rem;
}


